.header {
    @include flex(row, space-between, center);
    height: 68px;
    max-width: 95%;
    width: 100%;
    margin: 0 auto;

    @media screen and (max-width: 900px) {
        height: 40px;
    }

    img {
        height: 60%;

        @media screen and (max-width: 700px) {
            height: 40px;
        }
    }
}